/* reset */

*
{
    border: 0;
    box-sizing: content-box;
    color: inherit;
    list-style: none;
    margin: 0;
    padding: 0;
    text-decoration: none;
    vertical-align: top;
}


/* heading */

header input { font: bold 100% sans-serif; letter-spacing: 0.5em; text-align: center; text-transform: uppercase;
                background: #000; border-radius: 0.25em; color: #FFF; margin: 0 0 1em; padding: 0.5em 0; width: 100%;
}

/* table */

table { font-size: 75%; table-layout: fixed; width: 100%; }
table { border-collapse: separate; border-spacing: 2px; }
th, td { border-width: 1px; padding: 0.5em; position: relative; text-align: left; }
th, td { border-radius: 0.25em; border-style: solid; }
th { background: #EEE; border-color: #BBB;}

td { border-color: #DDD; }
textarea { border-radius: 0.25em; }

/* page */

html { font: 16px/1 'Open Sans', sans-serif; overflow: auto; padding: 0.5in; }
html { background: #999; cursor: default; }

body { box-sizing: border-box; height: 11in; margin: 0 auto; overflow: hidden; padding: 0.5in; width: 8.5in; }
body { background: #FFF; border-radius: 1px; box-shadow: 0 0 1in -0.25in rgba(0, 0, 0, 0.5); }

/* header */

header h1 { background: #000; border-radius: 0.25em; color: #FFF; margin: 0 0 1em; padding: 0.5em 0; }
header:after { clear: both; content: ""; display: table; }
header button {  width: 150px; height: 2em; border-style: solid; border-width: 1px; cursor: pointer;}

.logo { float: left; font-size: 125%; font-weight: bold; margin-left: 10%}

.features {
    float: right;
}

.download_btn{
    background-color: dodgerblue;
    color: white;
    border: none;
    padding: 5px;
    border-radius: 0.25em;
}

.download_btn:hover{ background-color: #113CFC}

.dropbtn { width: 150px; height: 2em; border-style: solid; border-width: 1px; background-color: white}
.dropbtn:hover{background-color: #f9f9f9;}
.dropdown { position: relative; display: inline-block; margin-top: 10px}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 150px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}

.dropdown-content p {
    color: black;
    font-size: 0.9em;
    padding: 5px;
    text-decoration: none;
    display: block;
}

.dropdown-content p:hover {background-color: #f1f1f1}

.dropdown:hover .dropdown-content {
    display: block;
}

/* article */

article, article address, table.meta, table.inventory { margin: 0 0 3em;}
article:after { clear: both; content: ""; display: table; }
article h1 { clip: rect(0 0 0 0); position: absolute; }

article textarea {
    padding: 0.4em; margin: 0.4em; border-style: solid; border-width: thin; resize: none;
    font-size: 0.7em; font-weight: normal;
}

article address { float: left; font-size: 125%; font-weight: bold; }

/*meta input*/
.meta_th {background: #EEE; font-weight: bold}

/* table meta & balance */

table.meta, table.balance { float: right; width: 40%; }
table.meta:after, table.balance:after { clear: both; content: ""; display: table; }

/* table meta */

table.meta th { width: 45%; text-align: center; }
table.meta td { width: 55%; }
table.meta input { width: 90%; padding: 0.3em; margin: 0}

/* table items */

table.inventory { clear: both; width: 100%; }
table.inventory th { font-weight: bold; text-align: center; }

table.inventory input { width: 100%;  padding: 0.2em}

.inventory_th { background: #EEE; text-align: center; font-weight: bold}
.totalPrice { background: white;}
/* table balance */

table.balance th, table.balance td { width: 50%; }
table.balance td { text-align: right; }

/* aside */

aside h1 { border: none; border-width: 0 0 1px; margin: 0 0 1em; }
aside h1 { border-color: #999; border-bottom-style: solid; }
aside:after { clear: both; content: ""; display: table; }
.additional {
    padding: 0.3em 0;
    width: 50%;
    float: left; font-size: 125%; font-weight: bold;
}

.additional_input {font-weight: normal; font-size: medium; text-align: left; margin: 0 .9em; padding: 0.2em 0.5em; float: left}

.bill_var {font-weight: lighter; font-size: medium; text-align: left; margin: 0 .3em; padding: 0.3em; float: left; width: 38%}
.bill_var_tax_dis {font-weight: lighter; font-size: medium; text-align: left; margin: 0 .3em; padding: 0.3em; float: left; width: 22%}

.bill_values {border-style: solid; border-width: 0.1em; padding: 0.3em; text-align: center; border-radius: 0.25em; float: right; width: 40%}


aside textarea {
    padding: 0.4em; margin: 0.8em 0; border-style: solid; border-width: thin; resize: none;
    font-size: 0.7em; font-weight: normal; width: 90%; height: 50%;
}

.bill { float: right; width: 40%}

.bill_details{
    margin: 1em 0;
    position: relative;
}

.bill strong{ float: right; padding: 0.3em;}

.bill i {float: right; padding: 0.2em; color: #1ba94c;}

.bill i:hover { color: darkgreen}

.options{  padding: 0.3em; margin: 0.3em}

/* javascript */

.add, .cut
{
    border-width: 1px;
    display: block;
    font-size: .8rem;
    padding: 0.25em 0.5em;
    float: left;
    text-align: center;
    width: 0.6em;
}

.add, .cut
{
    background: DodgerBlue;
    box-shadow: 0 1px 2px rgba(0,0,0,0.2);
    background-image: -moz-linear-gradient(#00ADEE 5%, #0078A5 100%);
    background-image: -webkit-linear-gradient(#00ADEE 5%, #0078A5 100%);
    border-radius: 0.5em;
    border-color: DodgerBlue;
    color: #FFF;
    cursor: pointer;
    font-weight: bold;
    text-shadow: 0 -1px 2px rgba(0,0,0,0.333);
}

.add {
    margin: -2.5em 2.5em 0;
}

.cut{
    margin: -2.5em 0 0;
}


.add:hover { background: #113CFC; }


.submit{
    background-color: DodgerBlue;
    border-radius: 0.25em;
    border: none;
    color: white;
    padding: 8px 30px;
    cursor: pointer;
    font-size: 20px;
}

.submit:hover{
    background-color: #113CFC;
}

@media print {
    * { -webkit-print-color-adjust: exact; }
    html { background: none; padding: 0; }
    body { box-shadow: none; margin: 0; }
    span:empty { display: none; }
    .add, .cut { display: none; }
}

@page { margin: 0; }

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
input[type=number] {
    -moz-appearance: textfield;
    margin: 0;
}